export const mediaList = mediaType => {
  let url = 'wp/v2/media/?_wpnonce=' + window.ajax_object.nonce
  if (mediaType) {
    url =
      'wp/v2/media/?media_type=' +
      mediaType +
      '&_wpnonce=' +
      window.ajax_object.nonce
  }
  return fetch(window.ajax_object.root + url, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      accept: 'application/json',
    },
  }).then(data => {
    return data.json()
  })
}
